
#login {
  padding: 10em;
}
html,
body,
.container-table {
  height: 100%;
  background-color: #282b30 !important;
}
.container-table {
  display: table;
  color: white;
}
.vertical-center-row {
  display: table-cell;
  vertical-align: middle;
}
.vertical-20p {
  padding-top: 20%;
}
.vertical-10p {
  padding-top: 10%;
}
.vertical-5p {
  padding-top: 5%;
}
.logo {
  width: 15em;
  padding: 3em;
}
.input-group {
  padding-bottom: 2em;
  height: 4em;
  width: 100%;
}
.input-group span.input-group-addon {
  width: 2em;
  height: 4em;
}
@media (max-width: 1241px) {
.input-group input {
    height: 4em;
}
}
@media (min-width: 1242px) {
form {
    padding-left: 20em;
    padding-right: 20em;
}
.input-group input {
    height: 6em;
}
}
.input-group-addon i {
  height: 15px;
  width: 15px;
}



